<template>
    <Row :gutter="16">
        <i-col :span="spancount" v-for="(item,index) in getBasicTemplateList" :key="index" >
            <Card :bordered="false" style="margin-top:10px;" v-if="index !== 0">
                <p slot="title">{{item.name}}</p>
                <img :src="item.smallImage" width="100%" height="250"/>
                <Divider />
                <p>
                    <Button v-if="usedbutton" @click="usedtemplate(index)">使用</Button>
                    <Button v-if="viewbutton" @click="viewtemplate(index)" :style="usedbutton ? {float:'right'} : ''">预览</Button>
                </p>
            </Card>
        </i-col>
    </Row>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'templateStore',
    props: {
        spancount: {
            type: Number,
            default: 4
        },
        usedbutton: {
            type: Boolean,
            default: false
        },
        viewbutton: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            basicTemplateList: []
        };
    },
    computed: {
        ...mapGetters([
            'getBasicTemplateList'
        ])
    },
    methods: {
        usedtemplate (index) {
            this.$emit('usedtemplateconfig', index);
        },
        viewtemplate (index) {
            debugger;
            this.$emit('viewtemplateconfig', index);
        }
    }

};
</script>
