<template>
    <Select  multiple  filterable  remote :remote-method="query">
        <Option v-for="(item,index) in data" :key="index" :value="item.id" >{{ item.name }}</Option>
    </Select>
</template>
<script>
export default {
    name: 'BatchSearchSelect',
    props: {
        width: {
            type: [Number, String],
            default: 150
        },
        onBatchSelectSearch: {
            type: Function,
            required: true
        }
    },
    data () {
        return {
            data: [],
            loading: false
        };
    },
    methods: {
        async query (value) {
            this.loading = true;
            try {
                this.data = await this.onBatchSelectSearch(value);
            } finally {
                this.loading = false;
            }
        }
    }
};
</script>
