<template>
    <Card>
        <Form ref="formValidate"  :model="formValidate" :rules="ruleValidate" :label-width="100" style="margin-top:20px;">
            <FormItem label="活动名称" prop="name" style="width: 600px">
                <i-input v-model="formValidate.name" placeholder="输入您要发布的活动名称"></i-input>
            </FormItem>
            <!-- <FormItem label="资源商" prop="epId">
            <SearchSelect :on-search="onSearch"  placeholder="选择资源商" v-model="formValidate.epId" />
        </FormItem> -->
            <FormItem label="活动开始时间" prop="startDate" >
                <DatePicker type="datetime" placeholder="选择开始时间和日期" style="width: 300px" v-model="formValidate.startDate" ></DatePicker>
            </FormItem>
            <FormItem label="活动结束时间" prop="endDate" >
                <DatePicker type="datetime" placeholder="选择结束时间和日期" style="width: 300px" v-model="formValidate.endDate" ></DatePicker>
            </FormItem>
            <FormItem label="积分有效开始" prop="pointsStartDate" >
                <DatePicker type="datetime" placeholder="选择开始时间和日期" style="width: 300px" v-model="formValidate.pointsStartDate"></DatePicker>
            </FormItem>
            <FormItem label="积分有效结束" prop="pointsEndDate" >
                <DatePicker type="datetime" placeholder="选择结束时间和日期" style="width: 300px" v-model="formValidate.pointsEndDate"></DatePicker>
            </FormItem>
            <FormItem label="活动类型" prop="type" style="width: 400px">
                <Select v-model="formValidate.type" placeholder="选择对应的资源商" >
                    <Option  v-for="(act,index) in ActivityType" :value="act.id" :key="index" >{{act.name}}</Option>
                </Select>
            </FormItem>
            <FormItem label="派发对象" >
                <Transfer
                    :data="shareEpList"
                    :target-keys="shareEpKeyList"
                    filterable
                    :filter-method="filterMethod"
                    @on-change="transferChange"></Transfer>
            </FormItem>
            <FormItem label="活动说明" prop="content">
                <i-input v-model="formValidate.content" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="输入活动的说明内容..."></i-input>
            </FormItem>
            <FormItem label="活动宣传模板" >
                <p v-if="getLastSavePageAttr.dataId === -1">
                    <Button @click="() => selectTemplate()">选择&编辑...</Button>
                    &nbsp;
                    <Button @click="() => useDefinedTemplate()">自定义内容...</Button>
                </p>
                <p v-if="getLastSavePageAttr.dataId !== -1">
                    {{getLastSavePageAttr.title}} <a @click="clearPublishAttr">清除</a>
                </p>
                <!-- <Button @click="historyQiniuList" style="margin-top:5px;">历史记录</Button> -->
                <!--  -->
            </FormItem>
            <FormItem label="立即发布？" prop="pubState">
                <i-switch size="large" @on-change="changePubState" v-model="formValidate.pubState">
                    <span slot="open">是</span>
                    <span slot="close">否</span>
                </i-switch>
            </FormItem>
            <FormItem label="活动发布时间"  prop="publishTime" v-if="isShow" >
                <DatePicker type="datetime" placeholder="选择活动的具体发布时间"  style="width: 300px" v-model="formValidate.publishTime"></DatePicker>
            </FormItem>
            <FormItem  v-if="!parentState">
                <Button type="primary" @click="handleSubmit('formValidate')" >提 交</Button>
                <Button @click="handleReset('formValidate')" style="margin-left: 8px" >重 置</Button>
            </FormItem>
        </Form>
        <Drawer
            title="模板库"
            v-model="showTemplateStore"
            width="720"
            :mask-closable="false"
            :styles="{ backgroundColor:'#eee'}"
        >
            <template-store :spancount="8" :usedbutton="true" :viewbutton="true" @usedtemplateconfig="(index) => usedtemplate(index)" @viewtemplateconfig="(index) => viewtemplate(index)"/>
        </Drawer>
        <Modal v-model="showTemplateCenter"  fullscreen  >
            <template-center :use-type="useType" :use-template-index="useTemplateIndex" ref="TemplateController" @publishsuccess="publishsuccess"/>
            <div slot="footer" >
            </div>
        </Modal>
    </Card>
</template>

<script>
import { ActivityType, RoleEpType } from '@/libs/dic';
import SearchSelect from '@/components/search-select';
import BatchSearchSelect from '@/components/search-batch-select';
import { DateFormat } from '@/libs/common';
import TemplateStore from '../temp/store';
import TemplateCenter from '../temp/index';
import { mapGetters } from 'vuex';

export default {
    name: 'ContentPublish',
    components: {
        SearchSelect,
        BatchSearchSelect,
        TemplateStore,
        TemplateCenter
    },
    props: {
        parentState: {
            type: Boolean,
            default: false
        }
    },

    data () {
        return {
            ActivityType,
            isShow: false,
            showTemplateCenter: false,
            useType: '',
            useTemplateIndex: -1,
            showTemplateStore: false,
            shareEpList: [],
            shareEpKeyList: [],
            formValidate: {
                name: '',
                startDate: '',
                endDate: '',
                pointsStartDate: '',
                pointsEndDate: '',
                type: '',
                content: '',
                publishTime: '',
                pubState: true
            },
            ruleValidate: {
                name: [
                    { required: true, message: '活动名称不能为空', trigger: 'blur' }
                ],
                epIds: [
                    { required: true, type: 'number', message: '活动分发商不能为空，并且必须从列表中选择', trigger: 'change' }
                ],
                startDate: [
                    { required: true, type: 'date', message: '活动的开始日期不能为空', trigger: 'change' }
                ],
                endDate: [
                    { required: true, type: 'date', message: '活动结束日期不能为空', trigger: 'change' }
                ],
                pointsStartDate: [
                    { required: true, type: 'date', message: '积分有效期开始日期不能为空', trigger: 'change' }
                ],
                pointsEndDate: [
                    { required: true, type: 'date', message: '积分有效期结束日期不能为空', trigger: 'change' }
                ],
                type: [
                    { required: true, type: 'number', message: '选择对应的活动类型', trigger: 'change' }
                ],
                content: [
                    { required: true, message: '活动内容不能为空', trigger: 'change' }
                ],
                publishTime: [
                    {
                        required: true,
                        type: 'date',
                        message: '发布时间不能为空',
                        trigger: 'blur'
                    }
                ]
            }
        };
    },
    computed: {
        ...mapGetters([
            'getLastSavePageAttr'
        ])
    },
    mounted () {
        this.getShareEpData();
    },
    methods: {

        historyQiniuList () {
            debugger;
        },
        selectTemplate () {
            if (this.formValidate.content.trim() === '') {
                this.$Message.warning('确认活动内容后才能编辑推广内容!');
                return;
            }
            this.showTemplateStore = true;
        },
        viewtemplate (index) {
            this.useType = 'view';
            this.showTemplateCenter = true;
            this.$refs.TemplateController.useTemplate(index);
        },
        usedtemplate (index) {
            this.useType = 'use';
            this.showTemplateCenter = true;
            this.$refs.TemplateController.useTemplate(index);
        },
        clearPublishAttr () {
            this.$store.commit('clearLastSavePageAttr');
        },
        useDefinedTemplate () {
            if (this.formValidate.content.trim() === '') {
                this.$Message.warning('确认活动内容后才能编辑推广内容!');
                return;
            }
            this.useType = 'use';
            this.showTemplateCenter = true;
            this.$refs.TemplateController.useTemplate(0);
        },
        transferChange (targetKeys, direction, moveKeys) {
            this.shareEpKeyList = targetKeys;
        },
        filterMethod (data, query) {
            return data.label.indexOf(query) > -1;
        },
        async getShareEpData () {
            const data = await this.get(`/api/nmedia/role/list?epType=${RoleEpType[1].id}&pageNum=1&pageSize=50`);
            for (const ep of data.value.list) {
                this.shareEpList.push({
                    key: ep.id,
                    label: ep.name
                });
            }
        },
        publishsuccess () {
            this.showTemplateCenter = false;
            this.showTemplateStore = false;
            this.$Notice.success({
                title: '内容保存成功',
                desc: '对应的活动发布成功后，可以上活动列表查看具体内容'
            });
        },
        handleSubmit (name) {
            this.$refs[name].validate(valid => {
                if (valid) {
                    this.submitData();
                } else {
                    this.$Message.error('表单数据验证不通过，请重新输入!');
                }
            });
        },
        async submitData () {
            debugger;
            if (this.shareEpKeyList.length === 0) {
                this.$Message.warning('活动分发商不能为空!');
                return;
            }
            if (this.getLastSavePageAttr.dataId === -1) {
                this.$Message.warning('活动对外的内容不能为空!');
                return;
            }
            debugger;
            this.formValidate.qiniuId = this.getLastSavePageAttr.dataId;
            this.formValidate.toIds = this.shareEpKeyList;
            const data = await this.post('/api/nmedia/content', this.formValidate);
            if (data.success) {
                this.$Message.success('活动发布成功!');
            } else {
                this.$Message.error('活动发布失败，请稍候再试!');
            }
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        async onSearch (val) {
            const result = await this.get(`/api/ep/list?epType=4&name=${val}`);
            return result.value;
        },
        async onBatchSelectSearch (val) {
            const result = await this.get(`/api/ep/list?epType=4&name=${val}`);
            return result.value;
        },
        changePubState (statue) {
            this.isShow = !this.isShow;
        },
        // 在父组件中触发，需要依赖
        initModifyData (item) {
            Object.keys(this.formValidate).forEach(key => this.formValidate[key] = item[key]);
            this.formValidate.startDate = DateFormat(item.startDate);
            this.formValidate.endDate = DateFormat(item.endDate);
            this.formValidate.pointsStartDate = DateFormat(item.pointsStartDate);
            this.formValidate.pointsEndDate = DateFormat(item.pointsEndDate);
            this.formValidate.publishTime = DateFormat(item.publishTime);
        },
        submitModifyData () {
            // 验证修改的提交数据，看父组件是否能分清作用域
            console.log(this.formValidate);
        }
    }
};
</script>
